.backdrop {
  z-index: 100;
  width: 100%;
  height: 100%;
  transition: opacity var(--transition-duration) var(--transition-timing-function), transform var(--transition-duration) var(--transition-timing-function), visibility var(--transition-duration) var(--transition-timing-function);
