}

.modal-form__alert--warning {
  color: #ffc039;
}

.modal-form__alert__icon {
  fill: currentColor;
  opacity: 0;
  transition: fill var(--transition-duration) var(--transition-timing-function);
  display: inline-flex;
  position: absolute;
  bottom: 12px;
  right: 10px;
  transform: scale(.3);
}

.modal-form__alert-icon--warning {
